import React from 'react'
import { useState, useEffect } from 'react'
import OptionButton from './OptionButton'

function Quiz({letter,setUserAnswer,PARFORM_AFFTER_STEPS,getClassName}) {
  const [active, setActive] = useState("")
  const [error, setError] = useState(false)
  useEffect(()=>{
    active !== "" && setError(false)
  })
  const options = [
    {
      name: "Root Leeter",
      id: "root-letter",
      value: 0,
    },
    {
      name: "Sky Letter",
      id: "sky-letter",
      value: 1
    },
    {
      name: "Grass Letter",
      id: "grass-letter",
      value: 2
    }
  ]
  return (
          <div className='quiz'>
          <h3 className='quiz-text'>
            Letter "{letter}" Is Blong To... 
          </h3>
          <div>
            <form onSubmit={(e)=> e.preventDefault()}>
              {
                options.map((item)=>{
                  return(
                    <OptionButton name={item.name} id={item.id} value={item.value} setUserAnswer={setUserAnswer} getClassName={getClassName} active={active} setActive={setActive}/>
                  )
                })
              }
             <p classNameerror>{error ? "Please Select an Option First" : ""}</p>
             <button className="check-button" onClick={()=> {
              if(active !== ""){
              PARFORM_AFFTER_STEPS()
              setActive("")
              } else {setError(true)}
            }}>Check</button>
              
            </form>
            
            
          </div>
        </div>
  )
}

export default Quiz